import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class FileService {

  constructor() { }

  basename(path: string) {
    const slashIndex = path.lastIndexOf('/');

    return path.substr(slashIndex + 1);
  }

  join(...args: any[]) {
    let path = args[0];
    const len = args.length;

    for (let i = 1; i < len; ++i) {
      const pathLen = path.length;
      let tok = args[i];

      if (tok[0] === '/') {
          tok = tok.replace('/', '');
      }

      if (path[pathLen - 1] === '/') {
          path += tok;
      } else {
          path += ('/' + tok);
      }
    }

    return path;
  }

  dirname(path: string) {
    if (!path) { return; }
    const slashIndex = path.lastIndexOf('/');
    if (slashIndex === 0) { return '/'; }
    return path.substr(0, slashIndex);
  }

  extension(path: string) {
    const dotIndex = path.lastIndexOf('.');

    if (dotIndex === -1) {
        return undefined;
    }

    return path.substr(dotIndex + 1);
  }

  hrefAppend(href: string, path: string) {
    const index = href.indexOf('?');
    let p = href.substring(0, index);

    p = this.join((p.length === 0 ? href : p), path);

    if (index !== -1) {
        return (p + href.substr(index));
    } else {
        return p;
    }
  }
}
